* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  display: block;
}

body {
  margin: 0;
}

.page__head {
  display: none;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
  font-weight: 400;
}

.card__pic {
  width: 160px;
  height: 134px;
  margin-top: 16px;
  margin-left: 3px;
  display: block;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 15px;
  font-size: 10px;
  line-height: 140%;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  margin: 3px 0 0;
  font-size: 10px;
  line-height: 140%;
}

.card__reviews-wrap, .card__price-wrap {
  justify-content: space-between;
  display: flex;
}

.card__price {
  color: #616070;
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 150%;
  display: inline-block;
}

.card__price_sum {
  color: #060b35;
  text-align: right;
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: inline-block;
}

.card__buy {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

